<template>
  <div class="app">
    <a-form :form="form" @submit="handleSubmit">
      <a-form-item
        class="form-item-title"
        label="新闻标题"
         
        :wrapper-col="{ span: 12 }"
      >
        <a-input
          v-decorator="[
          'note',
          {rules: [{ required: true, message: '请输入新闻标题!' }]}
        ]"
        />
      </a-form-item>
      <a-form-item
        class="form-item-title"
        label="新闻栏目"
        
        :wrapper-col="{ span: 12 }"
      >
        <a-select
          v-decorator="[
          'gender',
          {rules: [{ required: true, message: '请选择新闻栏目!' }]}
        ]"
          placeholder="选择栏目"
        >
          <a-select-option value="male">花花新闻</a-select-option>
          <a-select-option value="female">周边新闻</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item>
        <mceeditor v-model="Value"></mceeditor>
        Value:{{Value}}
      </a-form-item>
      <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
        <a-button type="primary" html-type="submit">Submit</a-button>
      </a-form-item>
    </a-form>
    <!--<mce-editor 
    :config           = "Config"
     v-model          = "Value"
    :url              = "Url" //图片上传地址
    :max-size         = "MaxSize"//文件上传最大值
    :accept           = "Accept"//接收什么格式的图片
    :with-credentials = false
    @on-ready         = "onEditorReady"//后面什么的没研究了
    @on-destroy       = "onEditorDestroy"
    @on-upload-success= "onEditorUploadComplete"
    @on-upload-fail   = "onEditorUploadFail"
    ></mce-editor>-->
  </div>
</template>

<script>
import mceeditor from "@/components/Tinymce";
window.tinymce.baseURL = "/static/tinymce";
window.tinymce.suffix = ".min";
export default {
  components: {
    mceeditor
  },
  data() {
    return {
      Value: "",
      formLayout: "horizontal",
      form: this.$form.createForm(this)
    };
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log("Received values of form: ", values);
        }
      });
    }
  }
};
</script>

<style>
.app {
  width: 1204px;
  margin: 0 auto;
  overflow: hidden;
}
.form-item-title {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
</style>


